    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80px;
    vertical-align: middle;
}

.tag-pill.tag-more {
    background-color: #94a3b8;
    color: #fff;
    cursor: help;
}

